import React from "react";
import BibleLink from "../BibleLink/BibleLink";

// –Ü–º–ø–æ—Ä—Ç—É–π—Ç–µ –≤–∞—à—ñ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∏ –¥–ª—è —Å—Ç–∏–ª—ñ–≤
const BoldText = ({ children }) => <strong style={{ color: "inherit" }}>{children}</strong>;
const ItalicText = ({ children }) => <em style={{ color: "inherit" }}>{children}</em>;
const QuoteText = ({ children }) => (
  <span
    style={{
      fontStyle: "italic",
      borderLeft: "2px solid #ccc",
      paddingLeft: "10px",
      margin: "0 0 10px 0",
    }}
  >
    {children}
  </span>
);

const renderSingleToken = (token, key) => {
  switch (token.type) {
    case "bold":
      return (
        <BoldText key={key}>
          <TokenRenderer tokens={token.content} />
        </BoldText>
      );
    case "italic":
      return (
        <ItalicText key={key}>
          <TokenRenderer tokens={token.content} />
        </ItalicText>
      );
    case "quote":
      return (
        <QuoteText key={key}>
          <TokenRenderer tokens={token.content} />
        </QuoteText>
      );
    case "bible-link":
      return (
        <BibleLink key={key} bibleRef={token.bibleRef}>
          {token.content}
        </BibleLink>
      );
    default:
      // ‚úÖ –ü–æ–≤–µ—Ä–Ω—É—Ç–æ React.Fragment
      return <React.Fragment key={key}>{token.content}</React.Fragment>;
  }
};

const TokenRenderer = ({ tokens }) => {
  if (!tokens) {
    return null;
  }

  // –Ø–∫—â–æ tokens ‚Äî —Ü–µ —Ä—è–¥–æ–∫, –ø—Ä–æ—Å—Ç–æ —Ä–µ–Ω–¥–µ—Ä–∏–º–æ –π–æ–≥–æ
  if (typeof tokens === "string") {
    // ‚úÖ –ü–æ–≤–µ—Ä–Ω—É—Ç–æ React.Fragment
    return <React.Fragment>{tokens}</React.Fragment>;
  }

  // –Ø–∫—â–æ tokens ‚Äî —Ü–µ –æ–¥–∏–Ω –æ–±'—î–∫—Ç-—Ç–æ–∫–µ–Ω (–Ω–µ –≤ –º–∞—Å–∏–≤—ñ)
  if (typeof tokens === "object" && !Array.isArray(tokens)) {
    return renderSingleToken(tokens, "single-token");
  }

  // –Ø–∫—â–æ tokens ‚Äî —Ü–µ –º–∞—Å–∏–≤, –æ–±—Ä–æ–±–ª—è—î–º–æ –∫–æ–∂–µ–Ω —Ç–æ–∫–µ–Ω
  if (Array.isArray(tokens)) {
    return tokens.map((token, index) => {
      // –Ø–∫—â–æ —Ç–æ–∫–µ–Ω ‚Äî —Ä—è–¥–æ–∫, —Ä–µ–Ω–¥–µ—Ä–∏–º–æ –π–æ–≥–æ —è–∫ —Ç–µ–∫—Å—Ç
      if (typeof token === "string") {
        // ‚úÖ –ü–æ–≤–µ—Ä–Ω—É—Ç–æ React.Fragment
        return <React.Fragment key={index}>{token}</React.Fragment>;
      }

      // –Ø–∫—â–æ —Ç–æ–∫–µ–Ω ‚Äî –æ–±'—î–∫—Ç, —Ä–µ–Ω–¥–µ—Ä–∏–º–æ –≤—ñ–¥–ø–æ–≤—ñ–¥–Ω–∏–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç
      return renderSingleToken(token, index);
    });
  }

  return null;
};

export default TokenRenderer;
