import React, { useContext } from "react";
import { motion, useAnimation } from "framer-motion";
import { BibleContext } from "../../contexts/BibleContext";
import {
  VerseButton,
  UnderlineClip,
} from "../SpecificContentDisplays/HomeGroupLessonDisplay/HomeGroupLessonDisplay.styled";

const BibleLink = ({ bibleRef, children }) => {
  const { navigateTo } = useContext(BibleContext);
  const controls = useAnimation();

  const handleLinkClick = e => {
    e.preventDefault();

    // 1) full -> 2) hide L‚ÜíR -> (–º–∏—Ç—Ç—î–≤–∏–π —Ä–µ—Å–µ—Ç) -> 3) show L‚ÜíR
    controls.start({
      clipPath: [
        "inset(0% 0% 0% 0%)", // –ø–æ–≤–Ω—ñ—Å—Ç—é –≤–∏–¥–Ω–æ
        "inset(0% 0% 0% 100%)", // –∑–Ω–∏–∫–∞—î –∑–ª—ñ–≤–∞ ‚Üí –≤–ø—Ä–∞–≤–æ (–∑—Ä—ñ–∑–∞—î–º–æ –∑–ª—ñ–≤–∞)
        "inset(0% 100% 0% 0%)", // –ø—Ä–∏—Ö–æ–≤–∞–Ω–æ, –∞–ª–µ "—è–∫—ñ—Ä" –∑–ª—ñ–≤–∞
        "inset(0% 0% 0% 0%)", // –∑‚Äô—è–≤–ª—è—î—Ç—å—Å—è –∑–ª—ñ–≤–∞ ‚Üí –≤–ø—Ä–∞–≤–æ
      ],
      transition: {
        duration: 0.6,
        times: [0, 0.45, 0.55, 1],
        ease: "linear",
      },
    });

    navigateTo(bibleRef);
  };

  return (
    <VerseButton
      as={motion.button}
      type="button"
      onClick={handleLinkClick}
      whileHover={{ scale: 1.03 }}
      whileTap={{ scale: 0.97 }}
      transition={{ duration: 0.15, ease: "easeOut" }}
      style={{ transformOrigin: "center center" }}
      aria-label={`–í—ñ–¥–∫—Ä–∏—Ç–∏ –ø–æ—Å–∏–ª–∞–Ω–Ω—è –Ω–∞ –ë—ñ–±–ª—ñ—é: ${children}`}
    >
      {children}
      <UnderlineClip initial={{ clipPath: "inset(0% 0% 0% 0%)" }} animate={controls} />
    </VerseButton>
  );
};

export default BibleLink;
